.logo-wrap {
  line-height: 60px;
  background: #002140;
  color: #fff;
  text-align: center;
}
.logo-wrap h2 {
  color: #fff;
}

.pageLayout {
  height: 100%;
}
.pageLayout .trigger {
  font-size: 18px;
  line-height: 64px;
  padding: 0 24px;
  cursor: pointer;
  -webkit-transition: color 0.3s;
  transition: color 0.3s;
}
.pageLayout .trigger:hover {
  color: #1890ff;
}

#root,
.App {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

